@import url("https://fonts.googleapis.com/css2?family=Merriweather:ital,opsz,wght@0,18..144,300..900;1,18..144,300..900&family=Montserrat:wght@100..900&family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap");

* {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
	user-select: none;
	-webkit-tap-highlight-color: transparent;
	font-family: "PT sans";
	color: #fff;
}

.App {
	height: 100vh;
	width: 100dvw;
	overflow: hidden;
	background-image: linear-gradient(to right, #a34d54, #a3625e, #a3766a, #8a9a6f, #6b8b8a, #74809b);
	animation: bgColorAnimation infinite 10s alternate-reverse linear;
	background-repeat: no-repeat;
	background-size: 300%;
	display: flex;
	flex-direction: column;
}

@keyframes bgColorAnimation {
	0% {
		background-position-x: left;
	}

	100% {
		background-position-x: right;
	}
}

.eye-image {
	display: flex;
	width: 170px;
	margin-top: 50px;
	cursor: pointer;
	position: absolute;
	transition: 0.7s;
	transform: translate(-50%);
}

.starting-button {
	background-color: #0000;
	max-width: max-content;
	border: 4px solid #fff;
	border-radius: 30px;
	font-size: 18px;
	padding: 12px 30px;
	transition-duration: 0.1s;
	cursor: pointer;
	display: flex;
	align-self: center;
	justify-self: center;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
}

#big-block {
	height: 600px;
	width: 600px;
	display: flex;
	justify-self: center;
	align-self: center;
	padding: 100px 0px;
	flex-direction: column;
}

.title {
	font-size: 40px;
	text-align: left;
}

.info {
	margin-top: 32px;
	font-size: 16px;
	text-align: left;
}

.info > p {
	margin: 16px 0px;
}

.get-info {
	margin-top: 16px;
	height: 55px;
	display: flex;
	align-items: center;
}

.get-info label {
	margin-left: 4px;
}

.start-button {
	display: inline-block;
	border: 4px solid #fff;
	color: #fff;
	text-decoration: none;
	border-radius: 30px;
	font-size: 18px;
	padding: 12px 30px;
	transition-property: border;
	transition-duration: 0.1s;
	background-color: #0000;
	margin-left: 24px;
}
